body {
  margin: 0;
  padding: 0;
  height: 100vh;
  box-sizing: border-box;
}

p, h1, h2, h3, h4, h5 {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
ul li {
  display: block;
}

hr {
  width: 100%;
  height: 2px;
  background-color: gold;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input[type=number] {
  -moz-appearance: textfield; /* Firefox */
}

.container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: 15px auto 0;
  border-radius: 5px;
  transition: opacity 0.2s linear;
}

.button {
  cursor: pointer;
  display: block;
  width: 50%;
  margin: 20px auto;
  padding: 10px 20px;
  text-align: center;
  color: white;
  font-size: 20px;
  background-color: blue;
  border: 0;
  border-radius: 5px;
  transition: transform 0.2s linear;
}
.button:hover {
  box-shadow: 3px 3px 0 darkblue;
  transform: translate(-3px, -3px);
}

.modal-window {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  display: block;
  max-width: 60vw;
  width: 100%;
  top: -100%;
  left: 0;
  right: 0;
  margin: 1em auto 0;
  padding: 1em;
  z-index: 1;
  border-radius: 15px;
  box-shadow: 0px 0px 5px black;
  background-color: wheat;
  transition: all 0.2s linear;
}
.modal-window.active {
  top: 0;
  opacity: 1;
  visibility: visible;
}
.modal-window__exit-button {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 20px;
  height: 20px;
  background-color: red;
  border-radius: 50%;
}

.overlay {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 1;
  background-color: rgba(255, 255, 255, 0.5);
}

.post {
  opacity: 0.8;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  text-align: center;
  font-size: 0;
  background-size: cover;
  border-radius: 15%;
  transition: transform 0.2s linear, color 0.2s linear, font-size 0.2s linear;
}
.post:hover {
  opacity: 1;
  font-size: 1em;
  font-weight: 700;
  transform: translateY(-5px);
  transition: transform 0.2s linear, color 0.2s linear, font-size 0.2s linear;
}

.post-window__add-comment {
  margin-top: 20px;
}

.image-section {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 20px;
}
.image-section__img {
  width: 90%;
  transform: scale(var(--scale));
  margin: auto 0;
  border-radius: 15px;
}
.image-section__title {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
}
.image-section__text {
  font-size: 1.2em;
}
.image-section__likes span {
  font-size: 1.2em;
  font-weight: 700;
}

.comments-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.comments-section__comment {
  margin-bottom: 20px;
}
.comments-section__title {
  display: flex;
  align-items: center;
}
.comments-section__avatar {
  width: 40px;
}
.comments-section__nickname {
  margin-left: 0.7em;
}
.comments-section__text {
  word-break: break-all;
}
.comments-section__show-comments-button {
  position: relative;
  width: 200px;
  opacity: 1;
  visibility: visible;
  margin: 20px auto 0;
  padding: 10px 15px;
}
.comments-section__show-comments-button.hidden {
  height: 0;
  opacity: 1;
  visibility: hidden;
  margin-top: 0;
  padding: 0;
}

.add-comment__name {
  text-align: center;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 5px;
  border: 0;
  box-shadow: 0px 1px 5px gray;
  transition: all 0.2s;
}
.add-comment__name:hover {
  cursor: text;
  background-color: #eee;
}
.add-comment__name:focus {
  outline: none;
  box-shadow: 0px 1px 5px #333;
}
.textarea {
  outline: none;
  resize: none;
  width: 100%;
  min-height: 1em;
  padding-left: 10px;
  border-radius: 5px;
  border: 0;
  box-shadow: 0px 1px 5px gray;
  transition: all 0.2s;
}
.textarea:hover {
  cursor: text;
  background-color: #eee;
  transition: all 0.2s;
}
.textarea:focus {
  cursor: text;
  box-shadow: 0px 1px 5px #333;
  transition: all 0.2s;
}

.add-button {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 15%;
  font-size: 1.5em;
  font-weight: 700;
  text-transform: uppercase;
  background-color: goldenrod;
  transition: all 0.2s linear;
}
.add-button:hover {
  color: wheat;
  transform: translateY(-10px);
  transition: transform 0.2s linear, color 0.3s linear;
}

.add-window__image-section {
  min-height: 400px;
}
.add-window__img-container {
  display: flex;
  justify-content: center;
  width: 90%;
}
.add-window__img {
  width: 90%;
  transform: scale(var(--scale));
  border-radius: 15px;
  transition: all 0.2s linear;
}
.add-window__upload-label {
  cursor: pointer;
  text-align: center;
  padding: 10px 20px;
  font-size: 20px;
  color: white;
  background-color: blue;
  border-radius: 5px;
  transition: transform 0.2s linear;
}
.add-window__upload-label:hover {
  box-shadow: 0 5px 0 darkblue;
  transform: translate(0, -5px);
}
.add-window__upload-input {
  position: absolute;
  opacity: 0;
  visibility: hidden;
}
.scale-control-settings {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}
.scale-control-settings__increase-button, .scale-control-settings__decrease-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  cursor: pointer;
  color: white;
  background-color: blue;
  border-radius: 100%;
  transition: all 0.1s linear;
}
.scale-control-settings__increase-button:hover, .scale-control-settings__decrease-button:hover {
  transform: scale(1.1);
  transition: all 0.1s linear;
}

.setting-section__filters {
  display: flex;
  justify-content: space-around;
  width: 100%;
}
.setting-section__filters .setting-section__label {
  font-size: 1.2em;
  cursor: pointer;
  padding: 5px 10px;
  border: 2px solid transparent;
  border-radius: 50px;
  transition: all 0.2s linear;
}
.setting-section__label {
  border: 2px solid blue;
}
.setting-section__label--active {
  color: white;
  background-color: blue;
}
.setting-section__input {
  display: none;
}
.range {
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0;
  margin: 0;
  transition: all 0.2s linear;
}
.range.active {
  opacity: 1;
  gap: 20px;
  margin: 20px 0;
}
.range__slider {
  width: 50%;
}
.range__input {
  width: 50px;
  text-align: center;
  border-radius: 10px;
  outline: 0;
  border: 2px solid transparent;
  transition: all 0.2s linear;
}
.range__input:hover {
  border: 2px solid blue;
}
.range__input:focus {
  border: 2px solid blue;
}

.hesh-tegs-section {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 20px;
}
.hesh-tegs-section__input {
  position: relative;
  width: 100px;
  height: 100%;
  padding: 5px 0 5px 10px;
  margin-right: 20px;
  outline: 0;
  border: 2px solid blue;
  border-radius: 10px;
}
.hesh-tegs-section__input::after {
  position: absolute;
  content: "111";
  right: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: red;
  border-radius: 0 10px 10px 0;
  transition: width 0.2s linear;
}
.hesh-tegs-section__hesh-tegs-container {
  display: flex;
  flex-wrap: wrap;
  column-gap: 20px;
  row-gap: 10px;
}
.hesh-tegs-section__hesh-teg {
  position: relative;
  padding: 2px 25px;
  overflow: hidden;
  font-size: 20px;
  color: white;
  background-color: blue;
  border-radius: 20px;
}
.hesh-tegs-section__hesh-teg:hover .hesh-tegs-section__close-button {
  width: 20px;
  font-size: 1rem;
}
.hesh-tegs-section__close-button {
  cursor: pointer;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  right: 0;
  top: 0;
  width: 0;
  height: 100%;
  font-size: 0;
  background-color: red;
  border-radius: 0 10px 10px 0;
  transition: width 0.2s linear;
}

.filter-chrome {
  filter: grayscale(1);
}

.filter-sepia {
  filter: sepia(1);
}

.filter-marvin {
  filter: invert(100%);
}

.filter-fobos {
  filter: blur(3px);
}

.filter-znoi {
  filter: brightness(3);
}

.filter-original {
  filter: none;
}